.material-page {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: transparent;
}

.main-material-title {
  position: absolute;
  z-index: 1000;
  font-family: 'NEXTMonoPan-Thin' !important;
  color: white;
  font-size: 20px;
  height: 0vh;
  left: 50%;
  top: 36%;
  transform: translate(-50%, -50%);
}

.bb {
  background-color: transparent;
}

.grid-container {
  width: 100%;
  height: 93.6%;
  z-index: 0;
}

.mini-item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.mini-item-material {
  opacity: 0.14;
}

.material-image {
  z-index: 100000000 !important;
  width: 11.8%;
  transition: all .5s;

  &.necklace {
    width: unset;
    height: 38%;

    &:hover {
      width: unset;
      height: 40%;
    }
  }

  &:hover {
    width: 12.7%;
  }
}

@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {

  .material-image {
    width: 30%;
  }

  .material-image:hover {
    width: 32%;
  }

  .type-mobile-material {
    visibility: visible;
    position: absolute;
    bottom: 0;
    color: #ffffff;
    background-color: #000000;
    width: 100%;
    height: 5.3vh;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    opacity: 0.85;
    font-size: 12px;
    font-weight: 100;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.86px;
    text-align: center;
  }

  .main-material-title {
    position: absolute;
    top: 0;
    z-index: 1000;
    font-family: 'NEXTMonoPan-Thin' !important;
    color: white;
    font-size: 14px;
    height: 17px;
    margin-top: 2.3vh;
    text-align: center;
    width: 100%;
    white-space: nowrap;
    padding: 0;
  }

}
