.container {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-family: 'NEXTMonoPan-Thin';
}

.version {
  position: absolute;
  top: 10px;
  right: -20px;
  color: white;
  width: 100px;
  font-size: 10px;
  font-weight: 500;
  opacity: .5;
  z-index: 100000;
}

.container::-webkit-scrollbar {
  display: none;
}

.question {
  z-index: 1000;
  font-family: 'NEXTMonoPan-Thin' !important;
  color: white;
  font-size: 20px;
  display: flex;
  justify-content: center;
  padding-top: 25vh;
}

.mobile-loading {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: #272727;
  opacity: 0;
  z-index: -1;
  animation-name: mobile-loading;
  animation-duration: 1.3s;
}

@keyframes mobile-loading {
  1% {
    z-index: 99999999;
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.bottom-bar {
  position: absolute;
  z-index: 10000000;
  bottom: 0;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {

  .container {
    overflow: hidden;
  }

  .logo {
    opacity: 0;
    z-index: -1;
    top: 35%;
    animation-name: mobile-loading;
    animation-duration: 1.3s;
  }

  .version {
    position: absolute;
    top: 50px;
    right: -40px;
    color: white;
    font-size: 10px;
    font-weight: 500;
    opacity: .5;
    z-index: 100000;
    transform: rotate(90deg);
    white-space: nowrap;
  }

  .question {
    position: absolute;
    top: 0;
    z-index: 1000;
    font-family: 'NEXTMonoPan-Thin' !important;
    color: white;
    font-size: 14px;
    height: 17px;
    margin-top: 2.3vh;
    text-align: center;
    width: 100%;
    white-space: nowrap;
    padding: 0;
  }

  .product-details {
    position: absolute;
    z-index: 100000;
    bottom: 2.8vh;
    left: 4.2vw;
    width: 91.46666vw;
    opacity: 0.95;
    border-radius: 2px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 12px;
    letter-spacing: 0.43px;

    .product-item {
      padding-top: 16px;
      padding-bottom: 8px;
      padding-left: 16px;
      overflow-wrap: anywhere;
      width: 200px;
      font-size: 12px;
      font-weight: 100;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.43px;
      color: #000000;
      display: flex;
      flex-wrap: wrap;
      width: 95%;

      .product-item-secret{
        word-break: keep-all;

        &:first-child {
          padding-right: 32px;
        }
      }

      .symbols-cost {
        word-break: keep-all;

        &:first-child {
          padding-right: 24px;
        }

        .letters-multiplications {
          font-size: 12px;
          color: rgba(0, 0, 0, 0.5);
        }
      }
    }

    .edit-botton {
      text-decoration: underline;
      padding-bottom: 22px;
    }

    .arrow {
      width: 20px;
      height: 20px;
      padding-left: 10px;
    }

    .buy-morse {
      background: #000000;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 101px;
      height: 36px;
      border-radius: 4px;
      letter-spacing: 0.6px;
      text-align: center;
      color: #ffffff;
      bottom: 16px;
      right: 16px;
    }
  }
}
