body {
  margin: 0;

  font-family: 'NEXTMonoPan-Thin';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url(./assets/images/circle/circle.png) 16 16, auto !important;
  overflow: hidden !important;
}

* {
  cursor: url(./assets/images/circle/circle.png) 16 16, auto !important;
}

@media(max-width: 600px) and (orientation: landscape) {
  html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
}


@font-face {
  font-family: 'TimesNow-Light';
  src: local('TimesNow-Light'), url('./assets/fonts/TimesNow-Light.otf') format('opentype');
}

@font-face {
  font-family: 'NEXTMonoPan-Thin';
  src: url(./assets/fonts/NEXTMonoPan-Thin.otf) format('opentype');
}

@font-face {
  font-family: 'NEXTMonoPan-ThinItalic';
  src: local('NEXTMonoPan-ThinItalic'), url('./assets/fonts/NEXTMonoPan-ThinItalic.otf') format('opentype');
}



