.grid-container {
  width: 100%;
  height: 100%;
  display: flex;
}

.main-grid-title {
  position: absolute;
  z-index: 1000;
  font-family: 'NEXTMonoPan-Thin' !important;
  color: white;
  font-size: 20px;
  height: 0vh;
  left: 50%;
  top: 36%;
  transform: translate(-50%, -50%);
}

.right-image-container {
  background: #4c4c4c;
}

.mouse-movement {
  position: absolute;
  color: #ffffff;
  text-shadow: 0 2px 16px #000000;
  align-self: center;
  text-align: center;
  letter-spacing: 1px;
  height: 17px;
  z-index: 1000;
  font-family: 'NEXTMonoPan-Thin';
  font-size: 14px;
  font-weight: 100;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
}

.mouse-movement:hover {
  pointer-events: none;

}

.mini-item {
  width: 50%;
  position: relative;
}

@keyframes mymove {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.first-card-background {
  background: transparent;
}


@keyframes mymovesecond {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


@keyframes mymove-chrome {
  from {
    height: 0px;
  }
  to {
    height: 100vh;
  }
}

@keyframes mymovesecond-chrome {
  from {
    height: 0px;
  }
  to {
    height: 90vh;
  }
}

@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
  @keyframes mymove {
    from {
      height: 0px;
    }
    to {
      height: 90vh;
    }
  }

  @keyframes mymove-chrome {
    from {
      height: 0px;
    }
    to {
      height: 78vh;
    }
  }

  @keyframes mymovesecond {
    from {
      height: 0px;
    }
    to {
      height: 30vh;
    }
  }

  @keyframes mymovesecond-chrome {
    from {
      height: 0px;
    }
    to {
      height: 26.3vh;
    }
  }

  .grid-container {
    z-index: 0;
  }

  .main-grid-title {
    position: absolute;
    top:0;
    z-index: 1000;
    font-family: 'NEXTMonoPan-Thin' !important;
    color: white;
    font-size: 14px;
    height: 17px;
    margin-top: 2.3vh;
    text-align: center;
    width: 100%;
    white-space: nowrap;
    padding: 0;
  }

  .second-grid-selected {
    flex-direction: column;
  }

  .second-grid {
    flex-direction: column;
    align-items: center;

    .mini-item {
      height: 30.65vh;
      animation-name: mobile-cards-spread !important;
      animation-duration: 0.5s;
    }

    @keyframes mobile-cards-spread {
      from {
        width: 60%;
      }
      to {
        width: 100%;
      }
    }
  }

  .mini-item {
    width: 100%;
    height: 91vh;
  }

  .mouse-movement {
    display: none;
    width: 0vh;
  }
}


