.card-wrapper {
  position: relative;
  height: 100%;
  //transition: all 1s;
  width: 100%;
  background: white;
  overflow: hidden;
  border-left: 1px solid #3a3b3c;
}

.card-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .8);
}

.hidden {
  visibility: hidden;
}

.off-line {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;

  .line {
    opacity: 0.04;
    width: 1px;
    background-color: #d8d8d8;
  }
}

.gradiant {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 400px;
  width: 100%;
  background: rgb(2, 0, 36);
  background: linear-gradient(0deg, rgba(2, 0, 36, 1) 0%, rgba(0, 0, 0, 0.5970763305322129) 0%, rgba(0, 212, 255, 0) 100%);
}

.layer {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url("../../../assets/bg-texture.png");
  background-repeat: repeat;
}

.card-cover:hover {
  display: none;
}

.item-column {
  background-repeat: no-repeat;
  background-size: auto 100% ;
  background-position: center;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
  opacity: 0.04;

  &.non-dark {
    opacity: 0.85;
  }

  &.morse-edit {
    background-size: 100% auto;
  }

  &.card-selection {
    background-size: cover;
  }

  &:hover {
    opacity: 0.85;
    transform: scale(1.1);
  }
}

.first-card-background {
  overflow-x: hidden;

  .item-column {
    background-size: auto 100% ;

  &.morse-edit {
    background-size: 100% auto;
  }

    &.card-selection {
      background-size: cover;
    }

  }
}

.full-width {
  width: 100vw !important;
  //transition: all 1s;

  .item-column {
    background-size: auto 100%;;

  &.morse-edit {
    background-size: 100% auto;
  }

    &.card-selection {
      background-size: cover;
    }
  }

  .center-woman {
  }

  .necklace-move {
    //background-position-y: -21vh;
  }
}

.no-width {
  height: 0 !important;
  width: 0 !important;
}

.full-light {
  filter: brightness(1);
}

.type-mobile {
  visibility: hidden;

}

@media(max-width: 600px) {

  .no-width {
    transition: all 1s;
    height: 0 !important;
    width: 0 !important;
  }

  .item-column {
    //opacity: 0.85;
  }

  .full-width {
    height: 90vh !important;
    width: 100vw !important;
    background-color: black;
    transition: all 1s;

    .item-column , .item-column.morse-edit {
      background-size: auto 100%;
      background-position: center;

      &:hover {
        transform:unset;
      }
    }
  }

  .card-wrapper {
    position: relative;
    background-size: cover;
    background-position: 50%;
    width: 100%;

    .item-column {
      .hide-line-mobile {
        visibility: hidden;
      }
    }

    .type-mobile {
      visibility: visible;
      position: absolute;
      background-color: #000000;
      width: 100%;
      height: 5.3vh;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #ffffff;
      z-index: 100000;
      bottom: 0;
      pointer-events: none;
      opacity: 0.85;
      font-size: 12px;
      font-weight: 100;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: 0.86px;
      text-align: center;
    }
  }

  .necklace-move {
    background-position-y: -30vh !important;
    background-color: black;
  }

  .other-move {
    background-position-y: -3vh !important;
    background-color: black;
  }
}
