.full-screen-menu {
  position: absolute;
  height: 100vh;
  width: 85vw;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100000 !important;
}

.morse-menu-wrapper {
  width: 16.73%;
  height: 100%;
  z-index: 50;
  background-color: black;
  opacity: 0.6;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100000 !important;
  overflow: hidden;
}

.list-wrapper {
  padding-top: 9.9vh;;
  display: flex;
  flex-direction: column;
  padding-left: 4.7vw;
}

.list-morse {
  color: rgba($color: #ffffff, $alpha: 0.8);
  font-size: 14px;
  text-align: center;
  font-family: 'TimesNow-SemiLight';
  line-height: 2.5vh;
}

.code {
  text-align: left;
  padding-left: 0;
}

.close-icon {
  position: absolute;
  top: 3.4vh;
  right: 2.4vw;
  background-image: url(../../../assets/images/x/x.png);
  width: 2.7vw;
  height: 3.9vh;
  background-repeat: no-repeat;
  z-index: 10002;
}

.hide-menu {
  display: none;
}

.bottom-bar-menu {
  position: absolute;
  bottom: 0vh;
  width: 100%;
  height: 3vh;
  font-size: 1vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: white;
}

.bottom-bar-menu-item {
  padding-left: 1vw;
  padding-right: 1vw;
}

.icon-menu {
  width: 8px;
  height: 8px;
  padding-right: 2px;
}

.dash-width {
  width: 20px;
  height: 8px;
}

.list-wrapper-mobile {
  visibility: hidden;
}

@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
  .morse-menu-wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.9;
  }

  .list-wrapper {
    display: none;
  }

  .list-wrapper-mobile {
    visibility: visible;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 90%;
  }

  .column-chars {
    height: 80%;
    margin: 25px;
  }

  .close-icon {
    top: 4vh;
    right: 5vw;
    width: 30px;
    height: 30px;
  }

  .bottom-bar-menu {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 5vh;
    font-size: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: white;

    .bottom-bar-menu-item {
      padding-left: 10vw;
    }
  }
}
