.container-morse {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
}

.container-morse-mini-text {
  @media (min-width: 600px) {
    .span-char-input {
      width: 4.5vw;
      font-size: 4.5vw;
    }
  }
}

.first-morse {
  filter: brightness(0.35);
}

.model-image {
  background-image: url(../../../assets/images/model-5.png);
  background-color: white;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;;
  width: 60%;
  z-index: 20000;
}

.logo {
  position: absolute;
  width: 111px;
  height: 72px;
  top: 44px;
  left: 50%;
  transform: translate(-50%, 0);
  background-image: url(../../../assets/images/logo/logo.svg);
  background-size: cover;
  z-index: 99999999;
  object-fit: contain;
}

.back-to-shop-card {
  position: absolute;
  top: 40vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 600px) {
    top: 34vh;
  }

  .description {
    position: relative;
    top: 74px;
    color: #ffffff;
    text-align: center;
    padding: 10px 0;
    height: 27px;
    font-size: 14px;
    font-weight: 100;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.93;
    letter-spacing: normal;
    text-align: center;

    @media (max-width: 600px) {
      height: 44px;
      font-size: 12px;
      line-height: 1.83;
      padding: 40px 10px;
    }
  }

  .back-to-shop {
    position: fixed;
    bottom: 20vh;
    height: 17px;
    background: black;
    color: #ffffff;
    opacity: 0.8;
    padding: 15px 25px;
    font-size: 14px;
    font-weight: 100;
    letter-spacing: 0.7px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    white-space: nowrap;
    text-align: center;

    img {
      padding-left: 8px;
    }

    @media (max-width: 600px) {
      display: none;
    }
  }
}

.morse-menu-icon {
  position: absolute;
  top: 40%;
  right: 0;
  width: 2vw;
  height: 9vh;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(../../../assets/images/tooltip/tooltip.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.no-morse-edit {
  animation-name: morse-fade-in;
  animation-duration: 0.2s;
}

@keyframes morse-fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.morse-input {
  z-index: 45;

  &:not(:first-child) {
    padding-top: 2vh;
  }
}

.paint-morse {
  position: absolute;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  z-index: 0;

  @media (max-width: 600px) {
    overflow: hidden;
  }


  .morse-border {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .tasti {
    color: grey;
    z-index: 10000;
    font-size: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2px;
    flex-direction: column;

    @media (max-width: 600px) {
      margin-bottom: 2px;
    }
  }

  @keyframes morse-new-letter {
    from {
      fill: white;
    }
    to {
      fill: rgba(255, 255, 255, 0.04);
    }
  }

  .morse-char {
    margin-bottom: 1px;
    animation-name: morse-new-letter;
    animation-duration: 1s;
    fill: rgba(255, 255, 255, 0.04);

    @media (max-width: 600px) {
      margin-bottom: 2px;
    }
  }

  .red-morse-fill {
    .morse-char {
      animation-name: unset;
      fill: #ff3232;
    }
  }

  .dash-svg {
    width: 4px;
    height: 12px;
  }
}

.buy-page {
  .morse-char {
    fill: white;
  }
}

.icon-background {
  width: 1.4vw;
  height: 1.4vh;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

.morse-btns {
  position: absolute;
  transform: translate(-50%, -50%);
  z-index: 1000;
  left: 50%;
  bottom: 0;
  padding-bottom: 5vh;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  font-family: 'NEXTMonoPan-Thin' !important;
  align-items: center;

  .next,
  .next-disable {
    color: white;
    opacity: 0.8;
    background-color: #000000;
    font-size: 14px;
    font-weight: 100;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    width: 175px;
    letter-spacing: 0.7px;
    border-radius: 2px;
  }

  .next-disable {
    opacity: 0.2;
  }

  .back, .back-disable {
    color: white;
    opacity: 0.8;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 16px;
    font-size: 14px;
    font-weight: 100;
    line-height: 3.57;
    letter-spacing: 0.7px;
  }

  .back-disable {
    opacity: 0.4;
  }

  .arrow {
    width: 20px;
    height: 20px;
    padding-left: 8px;
  }

  .left-arrow {
    padding-left: 8px;
    transform: scaleX(-1);
  }

  .next:hover, .back:hover {
    opacity: 1;
  }
}

@media(max-height: 700px) {
  .morse-btns {
    padding-bottom: 0;

    .back, .back-disable {
      margin-top: 0;
    }
  }
}

.morse-texts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
  padding-top: 2vh;
}

.display-none {
  display: none !important;
}

.buy-morse {
  background: #bbbbbb;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 40px;
  border-radius: 4px;
}


.buy-morse:hover {
  opacity: .9;
}

@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
  .morse-menu-icon {
    left: 3vw;
    top: unset;
    position: absolute;
    bottom: 9vh;
    width: 32px;
    height: 24px;
    background-image: url(../../../assets/tooltip-mobile.png);
  }

  .morse-btns {
    display: none;
  }

  .container-morse {
    .morse-texts {
      position: absolute;
      top: 21vh;
      width: 100%;
      padding: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}
